.form {
    margin-bottom: 30px;
    padding: 100px 0 30px 0;
    max-width: 500px;
    margin: 0 auto;
}

.container {
    max-width: 600px;
    margin: 0 auto;
}

.event {
    margin-bottom: 50px;
    font-size: 20px;
    font-weight: 500;
}

.items {
    margin-bottom: 35px;
}

.item {
    display: flex;
    flex-direction: column-reverse;
    gap: 5px;
    position: relative;
}

.item:not(:last-child) {
    margin-bottom: 15px;
}


.button {
    border: none;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 12px 12px;
    cursor: pointer;
    transition: all 150ms linear;
    color: #ffffff;
    background-color: #0fb8f5;
    text-transform: capitalize;
    font-size: 14px;
    font-weight: 500;
    line-height: 1.3;
    width: 100%;
    border-radius: 25px;
}

.button:hover {
    transition: all 150ms linear;
    background-color: #0095cb;
}


.input {
    background-color: transparent;
    border: 2px solid #636d74;
    border-radius: 40px;
    display: flex;
    width: 100%;
    padding: 10px 10px 10px 20px;
    align-items: center;
    color: #0fb8f5;
    font-size: 16px;
    transition: all 150ms linear;
}

.input:focus {
    transition: all 150ms linear;
    border: 2px solid #0fb8f5;
    outline: none;
}

.input:focus+.label {
    color: #0fb8f5;
    transition: all 150ms linear;
}

.label {
    color: #636d74;
    font-size: 13px;
    transition: all 150ms linear;
}

.icon {
    transition: all 150ms linear;
    background-color: transparent;
    border: none;
    z-index: 2;
    position: absolute;
    top: 50%;
    right: 3%;
    font-size: 20px;
    color: inherit;
}

.icon:hover {
color: #0fb8f5;
}